.OverlapPics_item{
    position: relative;

    width: 24px;
    height: 24px;
}

.OverlapPics_item_img{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    width: 36px;
    height: 36px;
    border: 2px solid var(--white);
    border-radius: 50%;
}